@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* フォントファミリー */
/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.3675213675vw;
  }
}
@media (min-width: 1170px) {
  html {
    font-size: 16px;
  }
}

body {
  display: flex;
  flex-direction: column;
  font-family: "";
}

main {
  flex: 1;
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

.c-test {
  font-size: 1.25rem;
  background-color: pink;
}

.p-carousel {
  width: 100%;
  padding: 2.5rem 0 4.75rem;
  overflow: hidden;
}

.p-carousel__wrapper {
  display: flex;
}

.p-carousel__images {
  display: flex;
  flex-shrink: 0;
}

.p-carousel__image {
  margin-left: 0.3125rem;
  flex-shrink: 0;
}
@media screen and (min-width: 768px) {
  .p-carousel__image {
    margin-left: 0.625rem;
  }
}

.p-carousel__images:first-child {
  animation: slideLoop1 90s linear infinite;
}

.p-carousel__images:nth-child(2) {
  animation: slideLoop2 90s -60s linear infinite;
}

.p-carousel__images:last-child {
  animation: slideLoop3 90s -30s linear infinite;
}

@keyframes slideLoop1 {
  0% {
    transform: translateX(200%);
  }
  to {
    transform: translateX(-100%);
  }
}
@keyframes slideLoop2 {
  0% {
    transform: translateX(100%);
  }
  to {
    transform: translateX(-200%);
  }
}
@keyframes slideLoop3 {
  0% {
    transform: translateX(0%);
  }
  to {
    transform: translateX(-300%);
  }
}
.p-carousel__image--up {
  transform: translateY(-1.25rem);
}
@media screen and (min-width: 768px) {
  .p-carousel__image--up {
    transform: translateY(-2.5rem);
  }
}

.p-carousel__image--down {
  transform: translateY(4.75rem);
}
@media screen and (min-width: 768px) {
  .p-carousel__image--down {
    transform: translateY(2.5rem);
  }
}

.p-carousel__image img {
  width: auto;
  height: 12.5rem;
}
@media screen and (min-width: 768px) {
  .p-carousel__image img {
    height: 25rem;
  }
}

.l-inner {
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  max-width: 36.875rem;
  padding-right: 1.5625rem;
  padding-left: 1.5625rem;
}
@media screen and (min-width: 768px) {
  .l-inner {
    max-width: 73.125rem;
    padding-right: 1.5625rem;
    padding-left: 1.5625rem;
  }
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

img.u-no-image {
  padding: 0.5rem;
  background-color: #ECECEC;
  object-fit: contain;
}
@media screen and (min-width: 768px) {
  img.u-no-image {
    padding: 1rem;
  }
}

.u-text-block {
  display: inline-block;
}